import { Avatar, Button, Card, Image } from 'antd'
import axios from 'axios';
import React from 'react'
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom'
import { setUser } from '../Auth/reducer/AuthReducer';
import { UserOutlined } from '@ant-design/icons';
import NavBarComponent from '../NavBar/NavBar';
import { Container } from './style';

const NotificationPage = () => {
    const navigate = useNavigate();
    const BASE_URL = process.env.REACT_APP_BASE_URL
const dispatch = useDispatch();
//state data 
const userData = useSelector((state) => state.Auth.user);
const token = useSelector((state) => state.Auth.token);

    useEffect(()=>{
      // return ()=>{
        setRead();
      // }
      
    },[])
    const setRead = async()=>{
      console.log(userData,'userData')
     const response = await axios.put(`${BASE_URL}user/notifications/${userData?._id}`,{},{
        
          headers: {
            Authorization: "Bearer " + token,
          },
          params: {
            id: userData._id,
          },
        
      })
      console.log(response)
       dispatch(setUser(response.data))
    }
  return (
   <>
   <NavBarComponent data={userData}/>
   <Container style={{marginTop:'60px'}}>
 <Card style={{display:'flex',justifyContent:'center'}}>
  <div style={{display:'flex',flexDirection:'column',rowGap:'20px',justifyContent:'center'}}>
  {userData?.notifications?.map((obj,i)=>{
    return(
      <div style={{display:'flex',gap:'20px',alignItems:'center'}} key ={i}>
        <div>
             <Avatar icon={obj?.userImg ? <Image preview={false} src={obj?.userImg}/>:<UserOutlined/>}/>
        </div>
        <div style={{maxWidth:'250px'}}>{obj?.message}</div>
        <div>
          {obj.post ? 
             <Avatar shape='square' size={'small'} icon={obj?.userImg ? <Image preview={false} src={obj?.post?.picturePath}/>:<UserOutlined/>}/>
           : '' }
             </div>
      </div>
    )

  })}
  </div>
 </Card>
 </Container>
    {/* <Button onClick={()=>navigate(-1)}>back</Button> */}
   </>
  )
}

export default NotificationPage